function randomUnit() {
    return _.sample(unitTypes)
}

const War = {
    id: 5,
    name: "It's WAR!!",
    units: () => _.range(0, 19).map(n => {
                    return { position: n, team: teams.red, x: 0 + parseInt(Math.random() * 8), y: n * 1 }
                }).concat(_.range(20, 39).map(n => {
                    return { position: n, team: teams.blue, x: 12 + parseInt(Math.random() * 8), y: (n - 20) * 1 }
                }))
}

const WarSetup = {
    map: 5,
    name: "meh",
    units: _.range(0,19).map( n => {
        return { position: n, type: randomUnit(), ai: ais.simpleAi}
    }).concat(_.range(20,39).map( n => {
        return { position: n, type: randomUnit(), ai: ais.simpleAi}
    }))
}

