export const maps = [
    FiveVsFive, FourVsFour, OneVsOne, Cod, War
]

export const scenarios = [
    CodSetup, FourSetup, FiveSetup, MGvsSgSetup, WarSetup
]

export function findMap(id: number): Object {
    return maps.filter(m => m.id === id)[0]
}

// combine maps object with setup object
export function createConfig(setup: Object): Object {
    const map = findMap(setup.map)
    let mapUnits = _.isFunction(map.units) ? map.units() : map.units
    let config = {
        map: map,
        setup: setup,
        units: mapUnits.map(unit => {
            let setupUnit = setup.units.filter(u => u.position === unit.position)[0]
            return {
                ...unit, ...setupUnit, x: metersToPixel(unit.x), y: metersToPixel((unit.y))
            }
        })
    }
    return config
}


